function Btns(props) {
	//  =========================
	//     iterable object 
	//   -   (for of )
	const abc = Array(4).keys();
	// console.log(abc);
	// for (const a of abc) console.log(a);
	const b = Array.from(abc);
	// console.log(b);
	// ============================
	const arr = Array.from(Array(props.num).keys());
	return (
		<ul className='scroll_navi'>
			{arr.map((_, idx) => {
				let active = '';
				idx === 0 ? (active = 'on') : (active = '');

				return (
					<li
						key={idx}
						className={active}
						onClick={() => props.setIndex(idx)}></li>
				);
			})}
		</ul>
	);
}

export default Btns;
