import { forwardRef, useState, useImperativeHandle } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

// 1.     () 
// 2.    forwardRef() wrapping
// 3. forwardRef   ref
const Popup = forwardRef((props, ref) => {
	//4.     state
	const [open, setOpen] = useState(false);
	//         
	// console.log(ref);
	//  forwardRef 
	// useImperativeHandle state 
	useImperativeHandle(ref, () => {
		return {
			open: () => setOpen(true), // 
			close: () => setOpen(false), // 
		};
	});

	// console.log(props);
	return (
		<>
			{/* open     null . */}
			{/* AnimatePresence :       */}
			<AnimatePresence>
				{open && (
					<motion.aside
						className='pop'
						initial={{ opacity: 0, scale: 0 }} // 
						animate={{ opacity: 1, transition: { duration: 1 }, scale: 1 }} //   
						exit={{ opacity: 0, scale: 0 }} //   
					>
						<motion.div
							className='con'
							initial={{ opacity: 0 }}
							animate={{ opacity: 1, transition: { delay: 1 } }}
							exit={{ opacity: 0 }}>
							{props.children}
						</motion.div>
						{/* <span
							className='close'
							onClick={() => {
								setOpen(false);
							}}>
							close
						</span> */}
					</motion.aside>
				)}
			</AnimatePresence>
		</>
	);
});

export default Popup;
