import Layout from '../common/Layout';
import { useRef, useState, useEffect } from 'react';

/* 
c - creat 
r - read  
u - update  
d - delete  
*/

function Community() {
	const input = useRef(null);
	const textarea = useRef(null);
	const editInput = useRef(null);
	const editTextarea = useRef(null);

	//     json 
	const getLocalData = () => {
		const data = localStorage.getItem('post');
		//    json  .
		return JSON.parse(data);
	};
	// const [posts, setPosts] = useState(dummyPosts);
	const [posts, setPosts] = useState(getLocalData);
	//   state
	const [allowed, setAllowed] = useState(true);

	// 
	const createPost = () => {
		if (!input.current.value.trim() || !textarea.current.value.trim()) {
			alert('  ');
			return;
		}
		setPosts([
			{ title: input.current.value, content: textarea.current.value },
			...posts,
		]);
		resetPost();
	};

	// 
	const resetPost = () => {
		input.current.value = '';
		textarea.current.value = '';
	};

	const deletePost = (index) => {
		// filter         
		// _     
		// setPosts(posts.filter((_, idx) => idx !== index));
		setPosts(
			posts.filter((_, idx) => {
				//          
				return idx !== index;
			})
		);
	};

	//    
	//      
	const enableUpdate = (index) => {
		//      
		setAllowed(false);
		setPosts(
			//           
			//enableUpdate:true   setPosts  state 
			posts.map((post, idx) => {
				if (idx === index) post.enableUpdate = true;
				return post;
			})
		);
	};

	//    
	const disableUpdate = (index) => {
		//     
		setAllowed(true);
		setPosts(
			posts.map((post, idx) => {
				if (idx === index) post.enableUpdate = false;
				return post;
			})
		);
	};
	// post  
	const updatePost = (index) => {
		if (!editInput.current.value.trim() || !editTextarea.current.value.trim()) {
			alert('   ');
		}
		//    
		setAllowed(true);
		setPosts(
			posts.map((post, idx) => {
				if (idx === index) {
					post.title = editInput.current.value;
					post.content = editTextarea.current.value;
					post.enableUpdate = false;
				}
				return post;
			})
		);
	};

	//posts    
	useEffect(() => {
		// post    state     
		// storage    JSON.stringify .
		localStorage.setItem('post', JSON.stringify(posts));
	}, [posts]);

	return (
		<Layout name='Community' img={'pic1.jpg'}>
			<div className='inputBox'>
				<input type='text' placeholder=' ' ref={input} />
				<br />
				<textarea
					cols='30'
					rows='10'
					placeholder=' '
					ref={textarea}></textarea>
				<br />
				<button onClick={resetPost}>cancle</button>
				<button onClick={createPost}>create</button>
			</div>
			<div className='showBox'>
				{posts.map((post, idx) => {
					return (
						<article key={idx}>
							{post.enableUpdate ? (
								// 
								<>
									<input
										type='text'
										defaultValue={post.title}
										ref={editInput}
									/>
									<br />
									<textarea
										cols='30'
										rows='10'
										defaultValue={post.content}
										ref={editTextarea}></textarea>

									<div className='btns'>
										<button onClick={() => disableUpdate(idx)}>cancle</button>
										<button onClick={() => updatePost(idx)}>save</button>
									</div>
								</>
							) : (
								// 
								<>
									<h2>{post.title}</h2>
									<p>{post.content}</p>

									<div className='btns'>
										{/*    enableUpdate   post  */}
										<button
											onClick={() => {
												// allowed true  
												if (allowed) enableUpdate(idx);
											}}>
											edit
										</button>
										<button onClick={() => deletePost(idx)}>delete</button>
									</div>
								</>
							)}
						</article>
					);
				})}
			</div>
		</Layout>
	);
}

export default Community;
