import Layout from '../common/Layout';
import Popup from '../common/Popup';
import { useEffect, useState, useRef } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Masonry from 'react-masonry-component';
import * as types from '../../redux/actionType';

function Flickr() {
	const { flickr } = useSelector((store) => store.flickrReducer);
	const dispatch = useDispatch();
	const path = process.env.PUBLIC_URL;
	const frame = useRef(null);
	const input = useRef(null);
	const pop = useRef(null);
	// saga  api  axios     state
	const [opt, setOpt] = useState({ type: 'interest', count: 100 });
	const [loading, setLoading] = useState(true);
	const [index, setIndex] = useState(0);
	const [enableClick, setEnableClick] = useState(true);

	const masonryOptions = {
		transitionDuration: '0.5s',
	};

	//        
	const endLoading = () => {
		setTimeout(() => {
			frame.current.classList.add('on');
			setLoading(false);
			setEnableClick(true);
		}, 1000);
	};

	const showSearch = (e) => {
		const result = input.current.value.trim();
		console.log(result);
		//    
		if (!result) return alert('');
		input.current.value = '';

		if (enableClick) {
			setEnableClick(false);
			setLoading(true);
			frame.current.classList.remove('on');

			//    
			// axios    setOpt state
			//  state  useEffect saga.js 
			console.log(result);
			setOpt({
				type: 'search',
				count: 50,
				tag: result,
			});
		}
	};

	useEffect(() => {
		//  opt   setOpt  axios    
		//  dispatch saga.js 
		dispatch({ type: types.FLICKR.start, opt });
		//    
	}, [opt]);

	//     endLoading()  effect  .
	//  endLoading api   
	// store    
	//   flickr        
	useEffect(() => {
		if (flickr.length !== 0) endLoading();
	}, [flickr]);
	return (
		<>
			<Layout name={'Flickr'} bg={`${path}/img/man1.jpg`} img={'pic3.jpg'}>
				{loading ? (
					<img className='loading' src={[path + 'img/loading.gif']} />
				) : null}
				<button
					onClick={() => {
						if (enableClick) {
							setEnableClick(false);
							setLoading(true);
							frame.current.classList.remove('on');
							setOpt({
								type: 'interest',
								count: 50,
							});

							// getFlickr({
							// 	type: 'interest',
							// 	count: 50,
							// });
						}
					}}>
					interest gallery
				</button>
				<button
					onClick={() => {
						if (enableClick) {
							setEnableClick(false);
							setLoading(true);
							frame.current.classList.remove('on');
							setOpt({
								type: 'search',
								count: 50,
								tag: 'glass building',
							});
						}
					}}>
					search gallery
				</button>
				<input
					type='text'
					ref={input}
					onKeyUp={(e) => {
						//     
						if (e.key === 'Enter') showSearch();
					}}
				/>
				<button
					onClick={() => {
						showSearch();
					}}>
					search
				</button>
				<div className='frame' ref={frame}>
					<div className='searchBox'></div>
					<Masonry elementType={'div'} options={masonryOptions}>
						{flickr.map((item, idx) => {
							return (
								<article key={idx}>
									<div className='inner'>
										<div
											className='pic'
											onClick={() => {
												setIndex(idx);
												pop.current.open();
											}}>
											<img
												src={`https://live.staticflickr.com/${item.server}/${item.id}_${item.secret}_m.jpg`}
												alt=''
											/>
										</div>
										<h2>{item.title}</h2>
										<div className='profile'>
											<img
												src={`http://farm${item.farm}.staticflickr.com/${item.server}/buddyicons/${item.owner}.jpg`}
												alt=''
												onError={(e) => {
													e.target.setAttribute(
														'src',
														'https://www.flickr.com/images/buddyicon.gif'
													);
												}}
											/>
											<span
												onClick={(e) => {
													if (enableClick) {
														setEnableClick(false);
														setLoading(true);
														frame.current.classList.remove('on');
														// user   
														// axios    setOpt state
														//  state  useEffect saga.js 
														setOpt({
															type: 'user',
															count: 50,
															user: e.currentTarget.innerText,
														});
													}
												}}>
												{item.owner}
											</span>
										</div>
									</div>
								</article>
							);
						})}
					</Masonry>
				</div>
			</Layout>
			<Popup ref={pop}>
				{flickr.length !== 0 ? (
					<>
						<img
							src={`https://live.staticflickr.com/${flickr[index].server}/${flickr[index].id}_${flickr[index].secret}_b.jpg`}
							alt=''
						/>
						<span className='close' onClick={() => pop.current.close()}>
							close
						</span>
					</>
				) : null}
			</Popup>
		</>
	);
}

export default Flickr;
